.wrapper {
  min-height: 500px;
  padding: 20px 0;
  .sort-wrapper {
    border-bottom: 1px solid grey;
  }
  .default-btn {
    margin-bottom: 30px;
  }
}
.pagination {
  display: flex;
  z-index: 11;
  width: 500px;
  margin: 30px auto 0 auto;
  li a {
    display: block;
    cursor: pointer;
    padding: 12px 20px;
    height: 46px;
    color: #4a92f0;
    height: 46px;
  }
  li {
    &:hover:not(.pagination_active) {
      background-color: rgb(238, 241, 245);
    }
  }
  &_active {
    background: rgb(74, 146, 240);
  }
  &_active-link {
    color: #fff !important;
  }
}
